const Slideshow = ({ images }: { images: string[] }) => {
  const [index, setIndex] = useState(0);

  useEffect(() => {
    const timer = setInterval(() => {
      setIndex((prev) => (prev + 1) % images.length);
    }, 2000);
    return () => clearInterval(timer);
  }, [images.length]);

  return (
    <Image
      src={images[index]}
      alt="slideshow"
      layout="fill"
      objectFit="cover"
      className="transition duration-500"
    />
  );
};

